<template>
  <div>
    <!--    面包屑导航-->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
<!--    卡片视图-->
    <el-card>
<!--      添加角色按钮区域-->
      <el-row>
        <el-col>
          <el-button type="primary" @click="dialogVisible=true">添加角色</el-button>
        </el-col>
      </el-row>
<!--      角色列表区域-->
      <el-table :data="rolesList" border stripe>
<!--        展开列-->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row :class="['border_bottom',index===0 ? 'border_top': '','center']" v-for="(item,index) in scope.row.children" :key="item.id">
<!--              渲染一级权限-->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,item.id)">{{item.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
<!--              渲染二三级权限-->
              <el-col :span="19">
                <el-row :class="[index1===0 ? '' :'border_top','center']" v-for="(item1,index1) in item.children" :key="item1.id">
                  <el-col :span="6">
                    <el-tag closable @close="removeRightById(scope.row,item1.id)" type="success">{{item1.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag type="warning" closable @close="removeRightById(scope.row,item2.id)" v-for="(item2,index2) in item1.children" :key="item2.id">
                      {{item2.authName}}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
<!--        索引列-->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)">编辑</el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeRolesById(scope.row.id)">删除</el-button>
            <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)">分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--    添加角色的对话框-->
    <el-dialog title="添加角色" :visible.sync="dialogVisible" width="40%" @close="addDialogClosed">
      <!--      对话框内容主题区域-->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
        <el-form-item label="角色名称" prop="roleName" label-width="80px">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc" label-width="80px">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRole">确 定</el-button>
      </span>
    </el-dialog>
    <!--    修改角色对话框-->
    <el-dialog title="修改角色" :visible.sync="editDialogVisible" width="30%" @close="editDialogClose">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="角色名称" prop="roleName" label-width="80px">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc" label-width="80px">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editRoleInfo">确 定</el-button>
      </span>
    </el-dialog>
<!--    分配权限对话框-->
    <el-dialog title="分配权限" :visible.sync="setDialogVisible" width="50%" @close="setDialogClose">
<!--      树形控件-->
      <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allRights">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Roles",
  data(){
    return{
      rolesList:[],
      dialogVisible:false,
      addForm:{
        roleName:'',
        roleDesc:''
      },
      addFormRules:{
        roleName:[
          {required: true, message: '请输入角色名称', trigger: 'blur'},
          {min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur'}
        ],
        roleDesc:[
          {required: true, message: '请输入角色描述', trigger: 'blur'},
          {min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur'}
        ],
      },
      editDialogVisible:false,
      editForm:{

      },
      editFormRules:{
        roleName:[
          {required: true, message: '请输入角色名称', trigger: 'blur'},
          {min: 3, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur'}
        ],
        roleDesc:[
          {required: true, message: '请输入角色描述', trigger: 'blur'},
          {min: 6, max: 15, message: '长度在 5 到 15 个字符', trigger: 'blur'}
        ],
      },
      setDialogVisible:false,
    //  所有权限数据
      rightsList:[],
      //树形控件属性绑定对象
      treeProps:{
        label:'authName',
        children:'children'
      },
      //默认选中权限
      defKeys:[],
      //分配权限id
      roleId:''
    }
  },
  created() {
    this.getRolesList()
  },
  methods:{
    async getRolesList(){
      const {data:res}=await this.$http.get('roles')
      if (res.meta.status!==200){
        return this.$message.error('获取角色列表失败')
      }
      this.rolesList=res.data

      console.log(this.rolesList)
    },
    addDialogClosed(){
      this.$refs.addFormRef.resetFields();
    },
    addRole(){
      this.$refs.addFormRef.validate(async valid=>{
        if (!valid)
          return
        const {data:res}=await this.$http.post('roles',this.addForm)
        if (res.meta.status!==201){
          this.$message.error('添加用户失败')
        }
        this.$message.success('添加用户成功')
        //关闭对话框
        this.dialogVisible=false
        //重新获取数据
        this.getRolesList()
      })
    },
    editDialogClose(){
      this.$refs.editFormRef.clearValidate();
    },
    async showEditDialog(id){
      const {data:res}=await this.$http.get('roles/'+id)
      if(res.meta.status!==200){
        return this.$message.error('查询角色信息失败')
      }
      this.editForm=res.data
      console.log(this.editForm)
      this.editDialogVisible=true
    },
    editRoleInfo(){
      this.$refs.editFormRef.validate(async valid=>{
        if (!valid)
          return
        // 发起修改角色数据请求
        const {data:res}=await this.$http.put('roles/'+this.editForm.roleId,{
          roleName:this.editForm.roleName,
          roleDesc:this.editForm.roleDesc
        })
        if (res.meta.status!==200){
          return this.$message.error('角色更新失败')
        }
        //关闭对话框
        this.editDialogVisible=false
        //刷新数据列表
        this.getRolesList();
        //提示更新角色信息成功
        this.$message.success('角色数据更新成功')
      })
    },
    async removeRolesById(id){
      // 删除提示
      const res=await this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err=>err)
      //用户确认返回confirm 取消返回cancel
      // console.log(res)
      if (res!=='confirm'){
        return this.$message.info('已取消删除')
      }
      const {data:response}=await this.$http.delete('roles/'+id)
      if (response.meta.status!==200){
        return this.$message.error('用户删除失败')
      }
      this.$message.success('该用户已删除')
      this.getRolesList();
    },
    // 根据Id删除用户权限
    async removeRightById(role,rightId){
      const confirmResult=await this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err=>err)
      if (confirmResult!=='confirm'){
        return this.$message.info('取消删除')
      }
      const {data:res}=await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if (res.meta.status!==200){
        return this.$message.error('删除权限失败！')
      }
      this.$message.success('权限删除成功！')
      // this.getRolesList();
      role.children=res.data
    },
    //展示分配权限的对话框
    async showSetRightDialog(role){
      this.roleId=role.id
      //获取权限数据
      const {data:res}=await this.$http.get('rights/tree')
      if (res.meta.status!==200){
        return this.$message.error('获取权限数据失败')
      }
      this.rightsList=res.data
      console.log(this.rightsList)
      this.getLeafKeys(role,this.defKeys)
      this.setDialogVisible=true
    },
    // 通过递归获取所有三级权限的id，并保持到defKeys数组中
    getLeafKeys(node,arr){
      if (!node.children){
        return arr.push(node.id)
      }
      node.children.forEach(item=>
      this.getLeafKeys(item,arr)
      )
    },
    setDialogClose(){
      this.defKeys=[]
    },
    //分配权限
    async allRights(){
      const keys=[
          ...this.$refs.treeRef.getCheckedKeys(),
          ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr=keys.join(',')

      const {data:res}=await this.$http.post(`roles/${this.roleId}/rights`,{
        rids:idStr
      })
      if (res.meta.status!=200){
        return this.$message.error('分配权限失败')
      }
      this.$message.success('分配权限成功')
      this.getRolesList()
      this.setDialogVisible=false
    }
  }
}
</script>

<style lang="less" scoped>
  .el-tag{
    margin: 7px;
  }
  .border_top{
    border-top: 1px solid #eeeeee;
  }
  .border_bottom{
    border-bottom: 1px solid #eeeeee;
  }
  .center{
    display: flex;
    align-items: center;
  }
</style>